<template>
  <div v-if="flag">
    <div class="classify-title">分类</div>
    <van-tree-select :items="items"
                     height="calc(100vh - 50px)"
                     :active-id.sync="activeId"
                     @click-nav="getSubClassify"
                     :main-active-index.sync="activeIndex" />
    <betterScroll :probeType="3"
                  :pullUpLoad="true"
                  ref="scroll"
                  class="scroll">
      <div class="subClassify">
        <div v-for="(v,i) in subClassifyData"
             :key="i">
          <a :href="v.link">
            <img :src="v.image"
                 alt="">
            <span>{{v.title}}</span>
          </a>
        </div>
      </div>
    </betterScroll>

  </div>
</template>
<script>
import betterScroll from '../../components/betterScroll.vue'
export default {
  name: 'classify',
  data () {
    return {
      subClassifyData: [],
      items: [],
      activeId: 1,
      activeIndex: 0,
      flag: false
    }
  },
  methods: {
    getSubClassify (index) {
      this.$http.getSubClassifyData(this.items[index].maitKey)
        .then(res => {
          console.log(res)
          this.subClassifyData = res.data.list
          this.flag = true
          setTimeout(() => {
            this.$refs.scroll.scroll.refresh()
          })
        })
    },
    getClassifyData () {
      this.$http.getClassifyData()
        .then(res => {
          this.items = res.data.category.list.map((v, i) => {
            v.text = v.title
            delete v.title
            return v
          })
          this.getSubClassify(0)
        })
    }
  },
  created () {
    this.getClassifyData()
  },
  components: {
    betterScroll
  }
}
</script>
<style lang="scss" scoped>
::v-deep .scroll {
  overflow: hidden;
  position: absolute;
  top: 50px;
  right: 0;
  bottom: 50px;
  width: 66.666%;
}
.subClassify {
  display: flex;
  flex-wrap: wrap;
  span {
    float: left;
    font-size: 0.35rem;
  }
  img {
    width: 100%;
    height: 2.5rem;
    vertical-align: bottom;
  }
  div {
    width: 2.49999rem;
    height: 2.9rem;
    text-align: center;
  }
}
.classify-title {
  height: 50px;
  font-size: 0.4rem;
  line-height: 50px;
  text-align: center;

  color: white;
  background: #ff8198;
}
</style>
